"use client";
import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { fetchCarById } from "../../api/carServices";
import { Car } from "../../types";
import { FiArrowLeft } from "react-icons/fi";
import { motion, AnimatePresence } from "framer-motion";

export default function ViewCarPage() {
  // ... (existing state and useEffect code remains exactly the same)

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="p-6"
    >
      {/* ... (previous header and other elements remain the same) */}

      <motion.div
        initial={{ y: 20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.3 }}
        className="bg-white rounded-xl shadow-xl overflow-hidden border border-gray-100 hover:shadow-2xl transition-shadow duration-300"
      >
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Basic Information */}
          <motion.div
            whileHover={{ y: -5 }}
            transition={{ type: "spring", stiffness: 300 }}
            className="bg-gradient-to-br from-blue-50 to-purple-50 p-6 rounded-lg hover:shadow-lg transition-all duration-300 group"
          >
            <h2 className="text-xl font-semibold mb-4 flex items-center">
              <motion.span 
                animate={{ 
                  scale: [1, 1.2, 1],
                  opacity: [0.8, 1, 0.8]
                }}
                transition={{ 
                  duration: 2,
                  repeat: Infinity
                }}
                className="w-3 h-3 bg-blue-500 rounded-full mr-2"
              ></motion.span>
              Basic Information
            </h2>
            <div className="space-y-4">
              {[
                { label: "Plate Number", value: car.plateNumber },
                { label: "Owner Name", value: car.ownerName },
                { label: "Owner Phone", value: car.ownerPhone },
                { label: "Model", value: car.model },
                { label: "Car Type", value: car.carType }
              ].map((item, index) => (
                <motion.div
                  key={index}
                  initial={{ x: -20, opacity: 0 }}
                  animate={{ x: 0, opacity: 1 }}
                  transition={{ delay: 0.5 + index * 0.1 }}
                  whileHover={{ 
                    x: 5,
                    backgroundColor: "rgba(239, 246, 255, 0.5)"
                  }}
                  className="flex border-b border-gray-100 pb-3 px-2 rounded-md transition-all duration-200"
                >
                  <span className="font-medium text-gray-600 w-40 group-hover:text-blue-600 transition-colors">
                    {item.label}:
                  </span>
                  <motion.span 
                    whileHover={{ scale: 1.03 }}
                    className="text-gray-800 font-medium"
                  >
                    {item.value || '-'}
                  </motion.span>
                </motion.div>
              ))}
            </div>
          </motion.div>
          
          {/* Technical Details */}
          <motion.div
            whileHover={{ y: -5 }}
            transition={{ type: "spring", stiffness: 300 }}
            className="bg-gradient-to-br from-purple-50 to-blue-50 p-6 rounded-lg hover:shadow-lg transition-all duration-300 group"
          >
            <h2 className="text-xl font-semibold mb-4 flex items-center">
              <motion.span 
                animate={{ 
                  scale: [1, 1.2, 1],
                  opacity: [0.8, 1, 0.8]
                }}
                transition={{ 
                  duration: 2,
                  repeat: Infinity,
                  delay: 0.5
                }}
                className="w-3 h-3 bg-purple-500 rounded-full mr-2"
              ></motion.span>
              Technical Details
            </h2>
            <div className="space-y-4">
              {[
                { label: "Manufacture Year", value: car.manufactureYear },
                { label: "Motor Capacity", value: car.motorCapacity },
                { label: "Fuel Efficiency", value: `${car.kmPerLiter} km/L` },
                { label: "Total KM", value: `${car.totalKm} km` },
                { label: "Fuel Type", value: car.fuelType }
              ].map((item, index) => (
                <motion.div
                  key={index}
                  initial={{ x: -20, opacity: 0 }}
                  animate={{ x: 0, opacity: 1 }}
                  transition={{ delay: 0.6 + index * 0.1 }}
                  whileHover={{ 
                    x: 5,
                    backgroundColor: "rgba(245, 243, 255, 0.5)"
                  }}
                  className="flex border-b border-gray-100 pb-3 px-2 rounded-md transition-all duration-200"
                >
                  <span className="font-medium text-gray-600 w-40 group-hover:text-purple-600 transition-colors">
                    {item.label}:
                  </span>
                  <motion.span 
                    whileHover={{ scale: 1.03 }}
                    className="text-gray-800 font-medium"
                  >
                    {item.value || '-'}
                  </motion.span>
                </motion.div>
              ))}
            </div>
          </motion.div>
          
          {/* Status Information */}
          <motion.div
            whileHover={{ y: -5 }}
            transition={{ type: "spring", stiffness: 300 }}
            className="md:col-span-2 bg-gradient-to-r from-blue-50 to-purple-50 p-6 rounded-lg hover:shadow-lg transition-all duration-300"
          >
            <h2 className="text-xl font-semibold mb-4 flex items-center">
              <motion.span 
                animate={{ 
                  scale: [1, 1.2, 1],
                  opacity: [0.8, 1, 0.8]
                }}
                transition={{ 
                  duration: 2,
                  repeat: Infinity,
                  delay: 1
                }}
                className="w-3 h-3 bg-indigo-500 rounded-full mr-2"
              ></motion.span>
              Status Information
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <motion.div
                initial={{ scale: 0.9 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.8 }}
                whileHover={{ 
                  scale: 1.05,
                  boxShadow: "0 10px 25px -5px rgba(0, 0, 0, 0.1)"
                }}
                className="bg-white p-4 rounded-lg shadow-sm hover:bg-gray-50 transition-all"
              >
                <p className="font-medium text-gray-600 mb-2">Status</p>
                <motion.span 
                  whileHover={{ scale: 1.1 }}
                  className={`px-3 py-1 inline-flex text-sm leading-5 font-semibold rounded-full 
                    ${car.status === 'Active' ? 'bg-green-100 text-green-800 hover:bg-green-200' : 
                      car.status === 'Maintenance' ? 'bg-yellow-100 text-yellow-800 hover:bg-yellow-200' : 
                      'bg-red-100 text-red-800 hover:bg-red-200'} transition-colors`}
                >
                  {car.status}
                </motion.span>
              </motion.div>
              
              {/* Other status cards with similar hover effects */}
              {[
                { label: "Registered Date", value: new Date(car.registeredDate).toLocaleDateString() },
                { label: "Parking Location", value: car.parkingLocation },
                { label: "Created By", value: car.createdBy }
              ].map((item, index) => (
                <motion.div
                  key={index}
                  initial={{ scale: 0.9 }}
                  animate={{ scale: 1 }}
                  transition={{ delay: 0.85 + index * 0.05 }}
                  whileHover={{ 
                    scale: 1.05,
                    boxShadow: "0 10px 25px -5px rgba(0, 0, 0, 0.1)"
                  }}
                  className="bg-white p-4 rounded-lg shadow-sm hover:bg-gray-50 transition-all"
                >
                  <p className="font-medium text-gray-600 mb-2">{item.label}</p>
                  <motion.p
                    whileHover={{ x: 3 }}
                    className="text-gray-800"
                  >
                    {item.value}
                  </motion.p>
                </motion.div>
              ))}
            </div>
          </motion.div>
        </div>
      </motion.div>

      <motion.button
        whileHover={{ 
          scale: 1.05,
          background: "linear-gradient(to right, #4f46e5, #7c3aed)"
        }}
        whileTap={{ scale: 0.95 }}
        onClick={() => router.push("/tms-modules/admin/car-management/manage-cars")}
        className="mt-6 flex items-center px-6 py-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg shadow-lg hover:shadow-xl transition-all"
      >
        <motion.span
          whileHover={{ x: -3 }}
          className="flex items-center"
        >
          <FiArrowLeft className="mr-2" /> 
          <span>Back to Fleet Dashboard</span>
        </motion.span>
      </motion.button>
    </motion.div>
  );
}